button {
  position: relative;
  margin: auto;
  border-radius: 1rem;
  cursor: pointer;
  animation: button 0.5s ease;
  overflow: hidden;
}
button p {
  z-index: 1;
  position: relative;
  font-size: 1.1rem;
}
button.primary__btn {
  color: #fff;
  padding: 15px 40px;
  background-color: #21729A;
  border: 1px solid #21729A;
  transition: all 0.25s;
}
button.ghost__btn {
  color: #fff;
  padding: 15px 40px;
  background-color: transparent;
  border: 1px solid #fff;
  transition: all 0.25s;
}
button.ghost__btn::after {
  content: "";
  position: absolute;
  top: -25%;
  left: 48%;
  width: 10px;
  height: 100px;
  z-index: 0;
  transform: rotate(15deg);
  background-color: #fff;
  transition: all 0.25s;
  opacity: 0;
}
button.ghost__btn:hover {
  color: #21729A;
  transition: all 0.25s;
}
button.ghost__btn:hover::after {
  transform: rotate(15deg) scale(29, 2);
  opacity: 1;
}

@keyframes button {
  0% {
    opacity: 0;
    transform: translateY(-50px) scale(0.9);
  }
  100% {
    opacity: 1;
    transform: translateY(0px) scale(1);
  }
}/*# sourceMappingURL=Button.css.map */
